.charlist__item {
  box-sizing: border-box;
  margin: 0;
  width: 200px;
  height: 318px;
  background-color: #232222;
  filter: drop-shadow(5px 5px 10px rgba(0, 0, 0, 0.25));
  padding: 15px;
  cursor: pointer;
  transition: 0.3s transform;
}

.charlist__item:focus {
  box-shadow: 0 5px 20px #9f0013;
  transform: translateY(-8px);
}

.charlist__img {
  display: block;
  width: 200px;
  height: 200px;
  object-fit: cover;
  transform: translate(-15px, -15px);
}

.charlist__name {
  margin: 0;
  font-weight: bold;
  font-size: 22px;
  line-height: 29px;
  text-transform: uppercase;
  color: #fff;
  display: block;
  height: 90px;
  text-overflow: ellipsis;
  overflow: hidden;
}
